@font-face {
    font-family: 'Inter';
    font-style:  normal;
    font-weight: 400;
    font-display: swap;
    src: url("Inter-Regular.woff2") format("woff2"),
         url("Inter-Regular.woff") format("woff"),
         url("Inter-Regular.otf") format("otf"),
         url("Inter-Regular.ttf") format("ttf");
}

